//main.jsstore
var car=JSON.parse(localStorage.getItem('car') || '[]')
var store=new Vuex.Store({
	state:{
		//statusdata
		//storethis.$store.state
		car:car,//
		//{id:id,count,price,selected:false}
		count:0
	},
	mutations:{
		//storestatemutations
		//state
		//
		increment(state){
			state.count++
		},
		substract(state,obj){
			//,statecommit
			state.count-=(obj.c+obj.d)
		},
		//mutationsthis.$store.commit('')
		addToCar(state,goodsinfo){
			//storecar
			//
			//pushcar
			//
			var flag=false
			state.car.some(item=>{
				if(item.id == goodsinfo.id){
					item.count +=parseInt(goodsinfo.count)
					flag=true
					return true
				}
			})
			//flagfalsepush
			if(!flag){
				state.car.push(goodsinfo)
			}
			
			//carcarlocalStorage
			localStorage.setItem('car',JSON.stringify(state.car))
		},
		updateGoodsInfo(state,goodsinfo){
			state.car.some(item=>{
				if(item.id == goodsinfo.id){
					item.count = parseInt(goodsinfo.count)
					return true
				}
			})
			localStorage.setItem('car',JSON.stringify(state.car))
		},
		removeFormCar(state,id){
			state.car.some((item,i)=>{
				if(item.id == id){
					state.car.splice(i,1)
					return true
				}
			})
			localStorage.setItem('car',JSON.stringify(state.car))
		},
		updateGoodsSelected(state,info){
			state.car.some(item=>{
				if(item.id == info.id){
					item.selected = info.selected
				}
			})
			localStorage.setItem('car',JSON.stringify(state.car))
		}
	},
	getters:{
		//gettersstatus
		//mutations
		optCount:function(state){
			return 'count:'+state.count
		},//return gettersgetters
		//
		// getterscomputedstate
		//gettersgetters
		//
		getAllCount(state){
			var c=0
			state.car.forEach(item=>{
				c+=item.count
			})
			return c
		},	
		//
		getGoodsCount(state){
			var o={}
			state.car.forEach(item=>{
				o[item.id]=item.count
			})
			return o
		},
		getGoodsSelected(state){
			var o={}
			state.car.forEach(item=>{
				o[item.id]=item.selected
			})
			return o
		},
		//
		getGoodsCountAndAmount(state){
			var o={
				count:0,//
				amount:0//
			}
			state.car.forEach(item=>{
				if(item.selected){
					o.count +=item.count
					o.amount +=item.price * item.count
				}
			})
			return o
		}
	}
})//new Vuex.Store
